import React from 'react';

import { makeStyles } from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import HomeIcon from '@material-ui/icons/Home';
import SearchIcon from '@material-ui/icons/Search';
import SettingsIcon from '@material-ui/icons/Settings';
import useNavigation from 'src/lib/hooks/useNavigation';

import MyBottomNavigationBar from './../common/MyBottomNavigationBar';

const useStyles = makeStyles((theme) => ({
  root: {},
}));

type KuralActionBarProps = {};
const ActionBarItems = [
  {
    icon: <HomeIcon />,
    route: '/',
    label: 'Home',
  },
  {
    icon: <SearchIcon />,
    route: '/search',
    label: 'Search',
  },
  {
    icon: <FavoriteIcon />,
    route: '/favorite',
    label: 'Favorite',
  },
  {
    icon: <SettingsIcon />,
    route: '/settings',
    label: 'Settings',
  },
];

const KuralActionBar: React.FC<KuralActionBarProps> = (
  props: KuralActionBarProps
) => {
  const [route, setRoute] = React.useState(ActionBarItems[0].route);
  const classes = useStyles();
  const [navigateTo] = useNavigation();
  const navigateBar = (route: string): void => {
    setRoute(route);
    navigateTo(route)();
  };
  return (
    <MyBottomNavigationBar
      items={ActionBarItems}
      currentRoute={route}
      onChange={navigateBar}
      className={classes.root}
    />
  );
};

export default KuralActionBar;
