import React from 'react';

import { useDispatch } from 'react-redux';
import { setViewerRx } from 'src/state/category-viewer';

import { Kural } from '../../../data/types/kural';
import { getKuralsOfChapter } from '../../api/kural';

type useChapterType = (
  chapter?: number
) => [number, Kural[], () => void, () => void];
const useChapter: useChapterType = (chapterNo = 1) => {
  const [chapter, setChapter] = React.useState(chapterNo);
  const dispatch = useDispatch();
  const prevChapter = (): void => {
    let newChapter = chapter - 1;
    if (chapter === 1) {
      newChapter = 133;
    }
    updateChapter(newChapter);
  };
  const nextChapter = (): void => {
    let newChapter = chapter + 1;
    if (chapter === 133) {
      newChapter = 1;
    }
    updateChapter(newChapter);
  };
  const updateChapter = (chapterNo: number): void => {
    dispatch(setViewerRx('chapter', chapterNo));
    setChapter(chapterNo);
  };
  return [chapter, getKuralsOfChapter(chapter), prevChapter, nextChapter];
};

export default useChapter;
