/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { Section } from 'src/data/types/kural';

import { SECTION_MAP, SectionData } from '../../data';
import { isBetween } from './common';

const getSection = (section: number): Section => {
  return {
    id: section,
    value: SECTION_MAP[section].title,
  };
};

const getAllSections = (): Section[] => {
  return Object.keys(SECTION_MAP).map((k) => ({
    id: parseInt(k),
    value: SECTION_MAP[k].title,
  }));
};
type SectionDataWithId = { key: number } & { data: SectionData };
const findSectionByLitrature = (litrature: number): SectionDataWithId => {
  const sectionId = Object.keys(SECTION_MAP).find((sectionId) => {
    return isBetween(litrature, SECTION_MAP[sectionId].litIndex);
  })!;
  return { key: parseInt(sectionId), data: SECTION_MAP[sectionId] };
};

const findSectionByChapter = (chapter: number): SectionDataWithId => {
  const sectionId = Object.keys(SECTION_MAP).find((sectionId) => {
    return isBetween(chapter, SECTION_MAP[sectionId].chapIndex);
  })!;
  return { key: parseInt(sectionId), data: SECTION_MAP[sectionId] };
};

export {
  getSection,
  getAllSections,
  findSectionByLitrature,
  findSectionByChapter,
};
