import React from 'react';

// import { makeStyles } from '@material-ui/core';
import { useSelector } from 'react-redux';
import { Swipeable, EventData } from 'react-swipeable';
import { Thirukural } from 'src/lib/hooks/kural';
import { AppState } from 'src/state/reducers';

import ListKuralCard from './ListKuralCard';

// const useStyles = makeStyles((theme) => ({}));

type KuralsByChapterViewerProps = {
  className?: string;
};

const KuralsByChapterViewer: React.FC<KuralsByChapterViewerProps> = (
  props: KuralsByChapterViewerProps
) => {
  // const classes = useStyles();
  const { type, key } = useSelector((st: AppState) => st.categoryViewer);
  const [, kurals, nextChapter, prevChapter] = Thirukural.useChapter(
    type === 'chapter' ? key : 1
  );
  const swipeHandler = (event: EventData | { dir: 'Right' | 'Left' }): void => {
    if (event.dir === 'Right') {
      nextChapter();
    } else if (event.dir === 'Left') {
      prevChapter();
    }
  };
  return (
    <div className={props.className}>
      <Swipeable onSwiped={swipeHandler}>
        <ListKuralCard kurals={kurals} />
      </Swipeable>
    </div>
  );
};

export default KuralsByChapterViewer;
