import { Literature, Category, BaseType } from 'src/data/types/kural';

import { LITERATURE_MAP, SECTION_MAP } from '../../data';
import { fillRange } from './common';
import { findSectionByLitrature } from './section';

const getLiterature = (literature: number): Literature => {
  return {
    id: literature,
    value: LITERATURE_MAP[`${literature}`],
  };
};

const getLiteraturesBySection = (sectionNo: number): Literature[] => {
  const section = SECTION_MAP[`${sectionNo}`];
  if (!section) {
    throw new Error('SECTION not found');
  }
  return fillRange(section.litIndex).map((k) => getLiterature(k));
};

const validateLitrature = (literature: number): void => {
  if (literature < 1 || literature > 13) {
    throw new Error('invalid litrature number');
  }
};

const getLiteratureRoute = (
  literature: number
): Record<Exclude<Category, 'kural' | 'chapter'>, BaseType> => {
  const { key, data } = findSectionByLitrature(literature);
  return {
    section: { id: key, value: data.title },
    literature: getLiterature(literature),
  };
};

export {
  getLiterature,
  getLiteraturesBySection,
  validateLitrature,
  getLiteratureRoute,
};
