import React from 'react';

import { Container, makeStyles, Chip } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';
import { Thirukural } from 'src/lib/hooks/kural';
import { setViewerRx } from 'src/state/category-viewer';
import { AppState } from 'src/state/reducers';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  chipRoot: {
    minWidth: '15vw',
    fontSize: '0.7em',
    margin: `0 1vw 4px 0`,
    [theme.breakpoints.up('sm')]: {
      maxWidth: 200,
    },
  },
}));

export type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type KuralRouteChipProps = {};

const KuralRouteChip: React.FC<KuralRouteChipProps> = (
  props: KuralRouteChipProps
) => {
  const classes = useStyles(props);
  const dispatch = useDispatch();
  const viewer = useSelector((st: AppState) => st.categoryViewer);
  const useRouteFilter = {
    ...(viewer.type === 'section' ? { section: viewer.key } : {}),
    ...(viewer.type === 'literature' ? { literature: viewer.key } : {}),
    ...(viewer.type === 'chapter' ? { chapter: viewer.key } : {}),
    ...(viewer.type === 'kural' ? { kural: viewer.key } : {}),
  };
  const routes = Thirukural.useRoute(useRouteFilter);
  return (
    <Container classes={{ root: classes.root }}>
      {routes.map((item, index) => (
        <Chip
          size='small'
          label={item.value}
          onClick={(): void => {
            dispatch(setViewerRx(item.type, item.id));
          }}
          color={'primary'}
          classes={{ root: classes.chipRoot }}
          key={index}
        />
      ))}
    </Container>
  );
};

export default KuralRouteChip;
