import React from 'react';

import { makeStyles, Badge } from '@material-ui/core';
import ListItem from '@material-ui/core/ListItem';
import clsx from 'clsx';
import { Kural } from 'src/data/types/kural';

import KuralDetail from './KuralDetail';

const useStyles = makeStyles((theme) => ({
  listItem: {
    marginBottom: theme.spacing(1),
    [theme.breakpoints.up('sm')]: {
      cursor: 'pointer',
    },
  },
  kuralListItem: {
    flexDirection: 'row',
    alignItems: 'start',
    justifyContent: 'space-between',
  },
  kuralBadge: {
    opacity: 0.8,
    height: '1.5em',
    fontSize: '0.5em',
    top: theme.spacing(-1),
    left: 2,
  },
  badgeContainer: {
    position: 'absolute',
  },
  detailBar: {},
  kuralActionContainer: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  kuralAction: {
    borderRadius: 'unset',
    padding: 0,
    width: '1em',
    height: '1em',
  },
}));

type KuralCardProps = {
  kural: Kural;
};
const KuralCard: React.FC<KuralCardProps> = (props: KuralCardProps) => {
  const classes = useStyles();
  const [showDetail, toggleDetail] = React.useState(false);
  const [first, second] = props.kural.value.split('|');
  return (
    <>
      <ListItem
        divider={!showDetail}
        classes={{ root: clsx(classes.listItem, classes.kuralListItem) }}
        onClick={(): void => toggleDetail(!showDetail)}>
        <Badge
          color='primary'
          badgeContent={props.kural.id}
          anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
          classes={{ badge: classes.kuralBadge, root: classes.badgeContainer }}
          max={1330}></Badge>
        <div>
          <div className='first'>{first}</div>
          <div className='second'>{second}</div>
        </div>
      </ListItem>
      {showDetail && (
        <ListItem divider={true} classes={{ root: clsx(classes.listItem) }}>
          <KuralDetail kuralNo={props.kural.id} />
          {/* <div className={classes.detailBar}>
            <div className={classes.kuralActionContainer}>
              <IconButton size='small' classes={{ root: classes.kuralAction }}>
                <PlayCircleFilledIcon fontSize='small' color='primary' />
              </IconButton>
              <IconButton size='small' classes={{ root: classes.kuralAction }}>
                <InfoIcon fontSize='small' color='primary' />
              </IconButton>
            </div>
          </div> */}
        </ListItem>
      )}
    </>
  );
};

export default KuralCard;
