import React from 'react';

import { makeStyles } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';
import { BaseType } from 'src/data/types/kural';
import { getChaptersByLitrature } from 'src/lib/api/chapter';
import { getLiteraturesBySection } from 'src/lib/api/literature';
import { getAllSections } from 'src/lib/api/section';
import { ViewerType, setViewerRx } from 'src/state/category-viewer';
import { AppState } from 'src/state/reducers';

import ListView from './../common/ListView';
import KuralsByChapterViewer from './KuralsByChapterViewer';
import RouteViewer from './RouteViewer';

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(1),
  },
}));

type SubCategoryListType = Exclude<ViewerType, 'kural' | 'chapter'>;

const AllKuralViewer: React.FC = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { type, key } = useSelector((st: AppState) => st.categoryViewer);
  const items =
    type === 'section'
      ? getLiteraturesBySection(key)
      : type === 'literature'
      ? getChaptersByLitrature(key)
      : getAllSections();
  const onItemSelected = (
    item: BaseType,
    category: SubCategoryListType
  ): void => {
    const type =
      category === 'top'
        ? 'section'
        : category === 'section'
        ? 'literature'
        : 'chapter';
    dispatch(setViewerRx(type, item.id));
  };
  return (
    <div className={classes.root}>
      <RouteViewer />
      {type === 'chapter' && <KuralsByChapterViewer />}
      {(type === 'section' || type === 'literature' || type === 'top') && (
        <ListView
          items={items}
          onItemSelected={(item): void => onItemSelected(item, type)}
        />
      )}
    </div>
  );
};

export default AllKuralViewer;
