import React from 'react';

import { makeStyles } from '@material-ui/core';
import List from '@material-ui/core/List';
import { Kural } from 'src/data/types/kural';

import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  list: {
    paddingBottom: 0,
    '& li:last-child': {
      borderBottom: 'none',
    },
  },
}));

type ListKuralCardProps = {
  kurals: Kural[];
};

const ListKuralCard: React.FC<ListKuralCardProps> = (
  props: ListKuralCardProps
) => {
  const classes = useStyles();
  return (
    <List className={classes.list}>
      {props.kurals.map((k, i) => (
        // TODO:k.id
        <KuralCard key={i} kural={k} />
      ))}
    </List>
  );
};

export default ListKuralCard;

