/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { Chapter, Category, BaseType } from 'src/data/types/kural';

import { CHAPTER_MAP } from '../../data';
import { fillRange, isBetween } from './common';
import { validateLitrature, getLiterature } from './literature';
import {
  findSectionByLitrature,
  findSectionByChapter,
  getSection,
} from './section';

const getChapterByKural = (kural: number): number => {
  return Math.ceil(kural / 10);
};

const getChapter = (chapter: number): Chapter => {
  return {
    id: chapter,
    value: CHAPTER_MAP[`${chapter}`],
  };
};

const validateChapter = (chapter: number): void => {
  if (chapter < 1 || chapter > 133) {
    throw new Error('invalid chapter number');
  }
};

const getChapterInfo = (
  chapterNo: number
): Record<Exclude<Category, 'kural'>, number> => {
  const section = findSectionByChapter(chapterNo);
  const litId = Object.keys(section.data.litChapMap).find((litId) => {
    return isBetween(chapterNo, section.data.litChapMap[litId]);
  })!;
  return {
    section: section.key,
    literature: parseInt(litId),
    chapter: chapterNo,
  };
};

const getChaptersByLitrature = (litrature: number): Chapter[] => {
  validateLitrature(litrature);
  const section = findSectionByLitrature(litrature);
  return fillRange(section.data.litChapMap[`${litrature}`]).map((k) =>
    getChapter(k)
  );
};

const getChapterInfoWithLabel = (
  chapterNo: number
): Record<Exclude<Category, 'kural'>, BaseType> => {
  const { section, literature, chapter } = getChapterInfo(chapterNo);
  return {
    section: getSection(section),
    literature: getLiterature(literature),
    chapter: getChapter(chapter),
  };
};

export {
  getChapterByKural,
  getChapterInfo,
  validateChapter,
  getChapter,
  getChaptersByLitrature,
  getChapterInfoWithLabel,
};
