import { Kural, KuralMixinId, CategoryKeyValue } from 'src/data/types/kural';

import { KURALS_MAP } from '../../data';
import {
  getChapterByKural,
  getChapterInfo,
  validateChapter,
  getChapter,
} from './chapter';
import { getLiterature } from './literature';
import { getSection } from './section';

const getKural = (id: number): Kural => {
  return { id, value: KURALS_MAP[id].k };
};
const getKuralsOfChapter = (chapter: number): Kural[] => {
  validateChapter(chapter);
  const EndKural = chapter * 10;
  return Array.from(Array(10).keys())
    .map((i) => EndKural - i)
    .sort((a, b) => a - b)
    .map((kId) => ({ id: kId, value: KURALS_MAP[kId].k }));
};

const validateKuralNo = (kuralNo: number): void => {
  if (kuralNo <= 0 || kuralNo > 1330) {
    throw new Error('invalid kural number');
  }
};

const getKuralRoute = (kural: number): KuralMixinId => {
  validateKuralNo(kural);
  const chapter = getChapterByKural(kural);
  const { literature, section } = getChapterInfo(chapter);
  return {
    section,
    literature,
    chapter,
    kural,
  };
};

const getKuralRouteWithLabel = (kuralNo: number): CategoryKeyValue => {
  const { section, literature, chapter, kural } = getKuralRoute(kuralNo);
  return {
    section: getSection(section),
    literature: getLiterature(literature),
    chapter: getChapter(chapter),
    kural: getKural(kural),
  };
};

export { getKural, getKuralsOfChapter, getKuralRoute, getKuralRouteWithLabel };
