import React from 'react';

import { Avatar } from '@material-ui/core';
import { renderRoutes } from 'react-router-config';
import Image from 'src/assets/images/thirukural.png';

import MyAppBar from '../../components/common/MyAppBar';
import MyDrawer from '../../components/common/MyDrawer';
import KuralActionBar from '../../components/kural/KuralActionBar';
import useDrawer from './../../../lib/hooks/useDrawer';

const Main: React.FC<any> = (props: any) => {
  const [drawerState, toggleDrawer] = useDrawer();
  return (
    <div className='container'>
      <MyAppBar
        drawerToggle={toggleDrawer}
        title='Thirukural'
        // TODO: alt
        leftIcon={<Avatar alt='kural' src={Image} />}
      />
      <MyDrawer
        toggleDrawer={toggleDrawer}
        drawerProps={{
          open: drawerState.isOpened,
          swipeAreaWidth: 20,
          onOpen: (): void => {
            toggleDrawer();
          },
          onClose: (): void => {
            toggleDrawer(false);
          },
        }}
      />
      {renderRoutes(props.route.routes)}
      <KuralActionBar />
    </div>
  );
};

export default Main;
