interface Kural {
  s: number;
  l: number;
  c: number;
  k: string;
}

interface ChapterInfo {
  section: string;
  literature: string;
  chapter: string;
}

const fillRange = (range: [number, number]): number[] => {
  const out = [];
  for (let i = range[0]; i <= range[1]; i++) {
    out.push(i);
  }
  return out;
};

const isBetween = (item: number, range: [number, number]): boolean => {
  return item >= range[0] && item <= range[1];
};

export { fillRange, isBetween };
export type { Kural, ChapterInfo };
