import { Category } from 'src/data/types/kural';

export type ViewerType = Category | 'top';
export type CategoryViewerState = {
  key: number;
  type: ViewerType;
};

type UpdateViewerAction = {
  type: 'update-category';
  payload: CategoryViewerState;
};

// actions
export const setViewerRx = (
  type: ViewerType,
  key: number
): UpdateViewerAction => ({
  type: 'update-category',
  payload: {
    key,
    type,
  },
});

// Reducer
const defaultState: CategoryViewerState = {
  key: 1,
  type: 'chapter',
};

export const CategoryViewerReducer = (
  state = defaultState,
  action: UpdateViewerAction
): CategoryViewerState => {
  return {
    ...state,
    ...action.payload,
  };
};
