import { KuralMixinId, CategoryKeyValue } from 'src/data/types/kural';
import { getChapterInfoWithLabel } from 'src/lib/api/chapter';
import { getKuralRouteWithLabel, getKural } from 'src/lib/api/kural';
import { getLiteratureRoute } from 'src/lib/api/literature';
import { getSection } from 'src/lib/api/section';
import { ViewerType } from 'src/state/category-viewer';

type RouteProp = {
  id: number;
  value: string;
  type: ViewerType;
};
type useRouteType = (filter: Partial<KuralMixinId>) => RouteProp[];

const getRoutes: (categories: Partial<CategoryKeyValue>) => RouteProp[] = (
  categories
) => {
  const routes: RouteProp[] = [{ id: 0, value: 'Top', type: 'top' }];
  if (categories.section) {
    routes.push({ ...categories.section, type: 'section' });
  }
  if (categories.literature) {
    routes.push({ ...categories.literature, type: 'literature' });
  }
  if (categories.chapter) {
    routes.push({ ...categories.chapter, type: 'chapter' });
  }
  return routes;
};
const useRoute: useRouteType = (filter) => {
  if (filter.kural) {
    return getRoutes({
      ...getKuralRouteWithLabel(filter.kural),
      kural: getKural(filter.kural),
    });
  } else if (filter.chapter) {
    return getRoutes(getChapterInfoWithLabel(filter.chapter));
  } else if (filter.literature) {
    return getRoutes(getLiteratureRoute(filter.literature));
  } else if (filter.section) {
    return getRoutes({ section: getSection(filter.section) });
  }
  return getRoutes({});
};

export default useRoute;
